import React, { Component } from "react";
import styles from "./Navbar.module.css";
import {
  InputGroup,
  FormControl,
  DropdownButton,
  Dropdown,
  Button,
} from "react-bootstrap";

import KLK from "../../assets/images/logo-KLK.png";
// import Label from "../../assets/images/logo-KLK-3.png";
import Search from "../../assets/images/search.svg";

export default class Navbar extends Component {
  constructor() {
    super();
    this.state = {
      selectName: "พื้นที่ใกล้ฉัน",
      selectList: ["พื้นที่ใกล้ฉัน", "สถานที่ทั้งหมด"],
    };
  }

  render() {
    return (
      <div className={styles.container}>
        <div className={styles.imgContainer}>
          <img src={KLK} alt="KLK" />
        </div>
        <div className={styles.inputContainer}>
          <InputGroup>
            <DropdownButton
              as={InputGroup.Prepend}
              variant="secondary"
              title={this.state.selectName}
            >
              {this.state.selectList.map((data) => {
                return (
                  <Dropdown.Item
                    onClick={() => {
                      this.setState({
                        selectName: data,
                      });
                    }}
                  >
                    {data}
                  </Dropdown.Item>
                );
              })}
            </DropdownButton>
            <FormControl
              placeholder="ค้นหา ชื่อ ร้านอาหาร และเครื่องดื่ม ร้านธงฟ้า ร้านค้า OTOP และสินค้าทั่วไป"
              aria-label="Username"
              aria-describedby="basic-addon1"
            />
            <InputGroup.Append>
              {/* <Button variant="light" style={{ backgroundColor: "#f8f8f8" }}>
                <img
                  src={Search}
                  alt="Search"
                  style={{ width: 14, height: 14 }}
                />
              </Button> */}
              <div className={styles.searchIcon}>
                <img
                  src={Search}
                  alt="Search"
                  style={{ width: 14, height: 14 }}
                />
              </div>
            </InputGroup.Append>
          </InputGroup>
        </div>
      </div>
    );
  }
}
